.bg__container {
  width: 100%;
  background-image: var(--gradient_3);
  font-size: 3.5rem;
  font-weight: 500;
  color: var(--head-bg);
  margin: 6.25rem 0 0 0;
  padding: 3.125rem 0 0 0;
}
@media (min-width: 40em) {
  .bg__container {
    margin: 6.25rem 0 0 0;
  }
  .bg__container__limiter {
    max-width: 67.5rem;
    margin: 0rem auto 0;
    padding: 6.25rem 0 0 0;
  }
}
.bg__container__limiter__grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .bg__container__limiter__grid {
    grid-template-columns: 50% 50%;
    grid-template-rows: 1fr;
    width: 100%;
  }
}

.content__top__description__title {
  font-weight: 700;
}
.content__top__description__shlogan {
  font-size: 1.25rem;
}
.content__top__description__shlogan span {
  display: block;
  padding: 0.625rem 0;
}
@media (min-width: 40em) {
  .content__top__description {
    grid-column: 1/2;
  }
  .content__top__description__shlogan {
    font-size: 1.5rem;
    font-weight: 500;
  }
  .content__top__description__shlogan span {
    display: block;
    padding: 2.5rem 0 1.25rem 0;
  }
  .content__top__img {
    grid-column: 2/3;
  }
}
.content__top__img img {
  width: 100%;
}

.blog__title {
  color: var(--font_2);
}
.blog__list {
  color: var(--font_light_grey);
  padding: 1.3%;
}
@media (min-width: 40em) {
  .blog__list {
    padding: 1%;
  }
}

.blog_bg__container__limiter__grid {
  display: block;
}

.color_white {
  color: var(--head-bg) !important;
}

.blogin__title {
  padding-bottom: 1.25rem;
}

h2 {
  color: var(--font_light_grey_title);
  font-weight: 400;
}

.community__title {
  color: var(--head-bg);
}
.community__gridparent {
  color: var(--head-bg);
  display: grid;
  line-height: 2.2;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(6, 1fr);
  gap: 10px 10px;
}
@media (min-width: 71.875em) {
  .community__gridparent {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
.community__grid__container a {
  background-color: rgba(0, 0, 0, 0.2);
  display: block;
  width: 100%;
  text-align: center;
  border-radius: 0.25rem;
}

#razorpay_button {
  padding-top: 2%;
}

.followem__grid {
  line-height: 2.2;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(12, 1fr);
  gap: 10px 10px;
}
@media (min-width: 71.875em) {
  .followem__grid {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(12, 1fr);
  }
}
.followem__pad {
  padding-top: 2rem;
}
.followem__bgheight {
  height: 100%;
}
.followem__gridcontainer {
  height: 4.6875rem;
}
.followem__gridcontainer a {
  display: grid;
  height: 75%;
  grid-template-columns: 45% 55%;
  grid-template-rows: repeat(1, 1fr);
  gap: 0.625rem;
}
.followem__gridcontainer a .grid__container__logo svg {
  padding-top: 1.6%;
  height: 80%;
  width: auto;
  float: right;
}
.followem__gridcontainer a .grid__container__tile {
  align-self: center;
  text-align: start;
  font-size: 1.125rem;
}